	module.exports = {"data":{"allWordpressPost":{"edges":[{"node":{"id":"e5723c19-af3b-56c3-8210-8cecd639b698","title":"Large gender imbalance in funding given for cancer research","slug":"large-gender-imbalance-in-funding-given-for-cancer-research","date":"May 14, 2018","excerpt":"<p>Male researchers receive more than three times as much funding as female researchers Male researchers receive far greater funding for cancer research than their female equivalents, suggests a study published in the journal BMJ Open. Within the European Union (EU), women represent nearly half of the workforce and more than half of all university graduates, [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2018/05/14/large-gender-imbalance-in-funding-given-for-cancer-research/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"e95ed0fd-6fa2-54b2-8de0-d22b7f0da9f9","title":"Moderate to severe mid-life anxiety may be linked to later life dementia","slug":"moderate-to-severe-mid-life-anxiety-may-be-linked-to-later-life-dementia","date":"April 30, 2018","excerpt":"<p>But it’s not clear whether active treatment might curb risk, say researchers Moderate to severe mid-life anxiety may be linked to dementia in later life, suggests an analysis of the available published evidence in the online journal BMJ Open. But as yet, it’s not clear whether active treatment could curb this risk, and whether non-drug [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2018/04/30/moderate-to-severe-mid-life-anxiety-may-be-linked-to-later-life-dementia/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"7197ce33-4149-55f1-9239-6ef05508aaca","title":"Fetal exposure to moderate/high caffeine levels linked to excess childhood weight gain","slug":"fetal-exposure-to-moderate-high-caffeine-levels-linked-to-excess-childhood-weight-gain","date":"April 23, 2018","excerpt":"<p>Should mums-to-be cut out caffeine altogether, ask the researchers? Exposure to moderate to high caffeine levels while in the womb is linked to excess weight gain in early childhood, suggests a large observational study published in the online journal BMJ Open. The findings, which back general advice to limit caffeine intake while pregnant, prompt the [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2018/04/23/fetal-exposure-to-moderate-high-caffeine-levels-linked-to-excess-childhood-weight-gain/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"7a5dc0a6-0799-572c-87de-6180e4e0d269","title":"Alcohol intake may be linked to premenstrual syndrome (PMS)","slug":"alcohol-intake-may-be-linked-to-premenstrual-syndrome-pms","date":"April 23, 2018","excerpt":"<p>Around 1 in 10 cases worldwide might be associated with drinking, estimate the researchers Drinking alcohol may be linked to pre-menstrual syndrome, or PMS for short, suggests a pooled analysis of published study data in the online journal BMJ Open. Based on the global prevalence of alcohol drinking and their study findings, the Spanish researchers [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2018/04/23/alcohol-intake-may-be-linked-to-premenstrual-syndrome-pms/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"e193370e-992b-5f6b-9337-c4bb4dab82ba","title":"Huge variation in prescribing practice for gluten free foods in England","slug":"huge-variation-in-prescribing-practice-for-gluten-free-foods-in-england","date":"April 16, 2018","excerpt":"<p>Those in most deprived areas least likely to get a prescription Prescribing practice for gluten free foods in England varies hugely, and doesn&#8217;t seem to be driven by obvious medical factors, reveals research published in the online journal BMJ Open. And those living in the most deprived areas of the country are the least likely [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2018/04/16/huge-variation-in-prescribing-practice-for-gluten-free-foods-in-england/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"b5246940-9f88-56e2-9743-7a93a6e8aab7","title":"Top 10 Most Read in March: dietary fats and changes in cardiovascular risk factors, declaration of conflicts of interest by clinicians and the association between eating speed and obesity","slug":"top-10-most-read-in-march-dietary-fats-and-changes-in-cardiovascular-risk-factors-declaration-of-conflicts-of-interest-by-clinicians-and-the-association-between-eating-speed-and-obesity","date":"April 10, 2018","excerpt":"<p>March saw a varied selection of articles enter the Top 10 Most Read. In the top spot, and receiving quite a bit of attention on social media, is a randomised trial by Khaw et al looking at coconut oil, olive oil and butter as dietary fats and the changes in blood lipid profile, weight, fat distribution [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2018/04/10/top-10-most-read-in-march-dietary-fats-and-changes-in-cardiovascular-risk-factors-declaration-of-conflicts-of-interest-by-clinicians-and-the-association-between-eating-speed-and-obesity/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"6d9e25d8-8c99-505c-af39-bdc0acbdcc83","name":"Highly accessed","slug":"highly-accessed"}]}},{"node":{"id":"11bf7b66-6eb9-5712-be86-a6c7f045c1c5","title":"Poor practice on conflict of interest in NHS may be damaging trust in clinicians","slug":"poor-practice-on-conflict-of-interest-in-nhs-may-be-damaging-trust-in-clinicians","date":"March 26, 2018","excerpt":"<p>US-style statutory body needed to ensure all trusts have strong policies in place, say researchers Many NHS trusts in England are failing to log, track, or disclose information on conflicts of interest (COI) for their staff, so potentially undermining public trust in health professionals, suggests research published in the online journal BMJ Open. A statutory [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2018/03/26/poor-practice-on-conflict-of-interest-in-nhs-may-be-damaging-trust-in-clinicians/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"728cd263-2346-54a5-88f1-21ac749f5534","title":"Surgeries employing GPs with additional training in complementary medicine appear less likely to prescribe antibiotics","slug":"surgeries-employing-gps-with-additional-training-in-complementary-medicine-appear-less-likely-to-prescribe-antibiotics","date":"March 19, 2018","excerpt":"<p>Use of complementary/alternative medicine may help reduce over prescribing of antibiotics GP surgeries with doctors who also have training in complementary and alternative medicines appear to be less likely to prescribe antibiotics to patients and may hold the key to reducing over-prescribing of these drugs, suggests a study published in the journal BMJ Open. Inappropriate [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2018/03/19/surgeries-employing-gps-with-additional-training-in-complementary-medicine-appear-less-likely-to-prescribe-antibiotics/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"39282e88-f885-54dd-9ac6-82769a7b8e6e","title":"2016 junior doctor strikes in England had ‘significant impact’ on healthcare provision","slug":"2016-junior-doctor-strikes-in-england-had-significant-impact-on-healthcare-provision","date":"February 19, 2018","excerpt":"<p>Thousands of appointments cancelled; effects greatest when emergency care withdrawn The 2016 junior doctors strikes in England had a ‘significant’ impact on the provision of healthcare, with thousands of appointments cancelled, and significantly fewer admissions and A&amp;E attendances than expected, reveals research published in the online journal BMJ Open. The effects were greatest during the last of [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2018/02/19/2016-junior-doctor-strikes-in-england-had-significant-impact-on-healthcare-provision/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"f6ea1fa8-fb2e-512c-8abe-d9dff5d9d815","title":"Slow eating speed may be linked to weight loss","slug":"slow-eating-speed-may-be-linked-to-weight-loss","date":"February 12, 2018","excerpt":"<p>&#8230;Along with cutting out after dinner snacks and not eating within 2 hours of going to sleep Slowing down the speed at which you eat, along with cutting out after dinner snacks and not eating within 2 hours of going to sleep may all help to shed the pounds, suggests research published in the online journal BMJ [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2018/02/12/slow-eating-speed-may-be-linked-to-weight-loss/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"0256c020-43f8-5758-b2a7-e6b09108d05a","title":"Top 10 most read in January: patient satisfaction with hospital care and nurses, manual therapy for excessively crying infants and the timing of probiotic milk consumption during pregnancy and adverse outcomes","slug":"january_top_ten","date":"February 08, 2018","excerpt":"<p>January sees seven new papers enter BMJ Open’s top 10 most read articles. In at number one is Aiken et al, who investigate how patient perceptions of hospital care are associated with confidence in nurses and doctors, nurse staffing levels and hospital work environments. The authors found a strong association between patients’ perceptions of hospital [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2018/02/08/january_top_ten/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"6d9e25d8-8c99-505c-af39-bdc0acbdcc83","name":"Highly accessed","slug":"highly-accessed"}]}},{"node":{"id":"17555d4e-f566-571f-82ba-14b0c8f9d454","title":"Editors&#8217; picks of 2017","slug":"editors-picks-of-2017","date":"January 31, 2018","excerpt":"<p>&nbsp; BMJ Open has had an incredible 2017 &#8211; publishing more than 2,700 articles across the whole spectrum of medicine. The editorial team at BMJ Open have selected some of their highlights from our articles published throughout 2017. Enjoy! Emotions and alcohol consumption: Is it wine that is making you whine? Alcohol is a popular [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2018/01/31/editors-picks-of-2017/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"81455f1d-c264-5716-b8c6-0b6332960946","name":"Editors picks","slug":"editors-picks"}]}},{"node":{"id":"08827cde-ad8e-5604-956f-a14465d90679","title":"Women taking probiotics during pregnancy might have lower pre-eclampsia and premature birth risk","slug":"women-taking-probiotics-during-pregnancy-might-have-lower-pre-eclampsia-and-premature-birth-risk","date":"January 23, 2018","excerpt":"<p>But timing may be crucial, findings suggest Probiotics taken during pregnancy might help lower the risks of pre-eclampsia and premature birth, suggests observational research in the online journal BMJ Open. But timing may be crucial, the findings indicate. Pre-eclampsia, a condition in which the mother’s body mounts an exaggerated inflammatory response, affects up to 8 percent [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2018/01/23/women-taking-probiotics-during-pregnancy-might-have-lower-pre-eclampsia-and-premature-birth-risk/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"9cc6d46d-3421-5d8f-af15-6542234ad927","title":"Emotionally demanding workload and confrontational patients key stressors for GPs","slug":"emotionally-demanding-workload-and-confrontational-patients-key-stressors-for-gps","date":"January 11, 2018","excerpt":"<p>Bullying/unsupportive colleagues and fear of complaints add to the mix The emotional impact of their daily workload and confrontational patients are among the key stressors for family doctors in England, reveals an analysis of feedback from general practitioners (GPs), published in the online journal BMJ Open. Dysfunctional working relationships and unsupportive/bullying colleagues, combined with the [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2018/01/11/emotionally-demanding-workload-and-confrontational-patients-key-stressors-for-gps/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"a08bab88-d454-5c1f-9c90-6547f74f35e0","title":"Patients’ unfavourable views of hospital care strongly linked to nurse numbers","slug":"patients-unfavourable-views-of-hospital-care-strongly-linked-to-nurse-numbers","date":"January 11, 2018","excerpt":"<p>Increasing the headcount may boost satisfaction with quality of care, say researchers Patients’ unfavourable views of hospital care in England are strongly linked to insufficient numbers of nurses on duty, rather than uncaring staff, indicates observational research published in the online journal BMJ Open. Increasing the registered nurse headcount may boost satisfaction with the quality [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2018/01/11/patients-unfavourable-views-of-hospital-care-strongly-linked-to-nurse-numbers/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"a90b5af1-4f1c-5ba4-b65e-550791464627","title":"Rubber/plastic bullets used for crowd control can maim and kill","slug":"rubber-plastic-bullets-used-for-crowd-control-can-maim-and-kill","date":"December 18, 2017","excerpt":"<p>Given their inherent inaccuracy, they shouldn’t be used for this purpose, say researchers Rubber/plastic bullets used for crowd control can maim and kill, and given their inherent inaccuracy, they shouldn’t be used for this purpose, concludes a review of the available evidence, published in the online journal BMJ Open. Rubber/plastic bullets (kinetic impact projectiles) are [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2017/12/18/rubber-plastic-bullets-used-for-crowd-control-can-maim-and-kill/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"8f3dc6a4-804b-53b6-a3e3-2eca274fc419","title":"Top 10 Most Read in November: health and social care constraints in England, general practice as a career choice, and cost-effectiveness of interventions in preventing type 2 diabetes","slug":"top-10-most-read-in-november-health-and-social-care-constraints-in-england-general-practice-as-a-career-choice-and-cost-effectiveness-of-interventions-in-preventing-type-2-diabetes","date":"December 12, 2017","excerpt":"<p>November was an interesting month, with many new papers published: five of which reached this month&#8217;s Top 10 Most Read list. In the top spot is Watkins et al, with a time trend analysis looking at the effects of health and social care spending constraints on mortality in England. They found that spending constraints, especially [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2017/12/12/top-10-most-read-in-november-health-and-social-care-constraints-in-england-general-practice-as-a-career-choice-and-cost-effectiveness-of-interventions-in-preventing-type-2-diabetes/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"6d9e25d8-8c99-505c-af39-bdc0acbdcc83","name":"Highly accessed","slug":"highly-accessed"}]}},{"node":{"id":"d0367ccd-37fe-5046-a259-1ad74ff8994e","title":"Different types of alcohol elicit different emotional responses","slug":"different-types-of-alcohol-elicit-different-emotional-responses","date":"November 21, 2017","excerpt":"<p>Spirits most frequently associated with feelings of aggression, international survey shows Different types of alcohol elicit different emotional responses, but spirits are most frequently associated with feelings of aggression, suggests research published in the online journal BMJ Open. To explore the potential emotional factors underpinning alcohol preference the researchers drew on anonymised responses to  the [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2017/11/21/different-types-of-alcohol-elicit-different-emotional-responses/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"9e15418c-3824-5677-9b7d-c086c61ba5d0","title":"Health and social care spending cuts linked to 120,000 excess deaths in England","slug":"health-and-social-care-spending-cuts-linked-to-120000-excess-deaths-in-england","date":"November 15, 2017","excerpt":"<p>Over 60s and care home residents most at risk; changes in nurse numbers may be key The squeeze on public finances since 2010 is linked to nearly 120,000 excess deaths in England, with the over 60s and care home residents bearing the brunt, reveals the first study of its kind, published in the online journal BMJ [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2017/11/15/health-and-social-care-spending-cuts-linked-to-120000-excess-deaths-in-england/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"e920e1c6-f656-551f-8dbc-0e0f243686f1","title":"Primary care consultations last less than 5 minutes for half the world’s population","slug":"primary-care-consultations-last-less-than-5-minutes-for-half-the-worlds-population","date":"November 08, 2017","excerpt":"<p>But range from 48 seconds in Bangladesh to 22.5 minutes in Sweden Primary care consultations last less than 5 minutes for half the world’s population, but range from 48 seconds in Bangladesh to 22.5 minutes in Sweden, reveals the largest international study of its kind, published in the online journal BMJ Open. Shorter consultation times [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2017/11/08/primary-care-consultations-last-less-than-5-minutes-for-half-the-worlds-population/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"ae2108de-f8e8-57eb-9f65-a4eeb2ead96a","title":"Top 10 most read in October: Physician-pharmaceutical industry interactions, systematic reviews and unpublished data, and polypharmacy and falls in older adults","slug":"top-10-most-read-in-october-physician-pharmaceutical-industry-interactions-systematic-reviews-and-unpublished-data-and-polypharmacy-and-falls-in-older-adults","date":"November 06, 2017","excerpt":"<p>&nbsp; Four new entries, and two re-entries, made it into BMJ Open’s top 10 most read articles in October. Climbing back into the top position is a longitudinal cohort study by Xie et al., which examines the association between Proton Pump Inhibitors and the risk of all-cause mortality. New in at number five is a [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2017/11/06/top-10-most-read-in-october-physician-pharmaceutical-industry-interactions-systematic-reviews-and-unpublished-data-and-polypharmacy-and-falls-in-older-adults/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"6d9e25d8-8c99-505c-af39-bdc0acbdcc83","name":"Highly accessed","slug":"highly-accessed"}]}},{"node":{"id":"1adc071f-b63d-517d-808e-46493cd8179e","title":"Less red tape and shorter working hours might help stave off retirement of UK doctors","slug":"less-red-tape-and-shorter-working-hours-might-help-stave-off-retirement-of-uk-doctors","date":"October 31, 2017","excerpt":"<p>GPs most likely to stop working because of workload pressures, survey shows Less red tape and shorter working hours are the two key factors that might persuade older UK doctors to carry on working rather than hanging up their stethoscopes, suggests an analysis of survey responses, published in the online journal BMJ Open. Family doctors [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2017/10/31/less-red-tape-and-shorter-working-hours-might-help-stave-off-retirement-of-uk-doctors/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"16260222-d567-5ae2-9a3d-99921baea1d6","title":"1 in 4 women and 1 in 6 men aged 65+ will be physically disabled in Europe by 2047","slug":"1-in-4-women-and-1-in-6-men-aged-65-will-be-physically-disabled-in-europe-by-2047","date":"October 23, 2017","excerpt":"<p>Proportions not set to change over next 30 years, but absolute numbers will rise significantly By 2047 one in four women and one in six men aged 65 and above is expected to be living with a physical disability that will severely restrict everyday activities, reveals an analysis published in the online journal BMJ Open. [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2017/10/23/1-in-4-women-and-1-in-6-men-aged-65-will-be-physically-disabled-in-europe-by-2047/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"431a6745-354a-5a2f-b394-fa7d1df38615","title":"Physically active mid-lifers more likely to be active into old age","slug":"physically-active-mid-lifers-more-likely-to-be-active-into-old-age","date":"September 20, 2017","excerpt":"<p>Playing sport most likely to stand test of time &#8211; at least for men Men who are physically active in mid-life are more likely to continue the habit into older age as well, finds a long term tracking study published in the online journal BMJ Open. Playing sport is the physical activity most likely to [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2017/09/20/physically-active-mid-lifers-more-likely-to-be-active-into-old-age/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"cedf6883-853c-571b-a7d2-905f01f09f35","title":"Evidence for potential harms of light drinking in pregnancy ‘surprisingly’ limited","slug":"evidence-for-potential-harms-of-light-drinking-in-pregnancy-surprisingly-limited","date":"September 11, 2017","excerpt":"<p>But review confirms alcohol best avoided ‘just in case’, say researchers The evidence for the potentially harmful effects of light or occasional drinking in pregnancy is ‘surprisingly limited,’ but women are still better off avoiding all alcohol while pregnant, just in case, concludes a pooled analysis of the available data, published in the online journal [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2017/09/11/evidence-for-potential-harms-of-light-drinking-in-pregnancy-surprisingly-limited/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"8a35b98c-ee4c-5656-b0e4-89172be52793","title":"People with disabilities face major hurdles accessing healthcare in UK","slug":"people-with-disabilities-face-major-hurdles-accessing-healthcare-in-uk","date":"September 11, 2017","excerpt":"<p>Disabled women are particularly disadvantaged People with disabilities in the UK face major hurdles accessing healthcare, reveals research published in the online journal BMJ Open. Disabled women are particularly disadvantaged, the findings show around one in five (19%) people in the UK is thought to live with a disability, but little is known about their access [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2017/09/11/people-with-disabilities-face-major-hurdles-accessing-healthcare-in-uk/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"0f2afc4b-3feb-5c47-a378-085a19eb5cb7","title":"Improvised explosive devices inflict much more serious injuries than land mines","slug":"improvised-explosive-devices-inflict-much-more-serious-injuries-than-land-mines","date":"August 23, 2017","excerpt":"<p>Multiple amputations much more likely The types of close contact injuries inflicted by improvised explosive devices (IEDs) are much more serious than those associated with land mines, finds research published in the online journal BMJ Open. Landmines came into widespread use in the Second World War, where they were designed to injure/maim rather than to [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2017/08/23/improvised-explosive-devices-inflict-much-more-serious-injuries-than-land-mines/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"d121af45-3dd8-5b36-aa84-91e9e3c2259d","title":"Top 10 Most Read in July: Link Worker social prescribing, best practice in sex and relationship education, and menstrual hygiene management among Bangladeshi adolescent schoolgirls","slug":"top-10-most-read-in-july-link-worker-social-prescribing-best-practice-in-sex-and-relationship-education-and-menstrual-hygiene-management-among-bangladeshi-adolescent-schoolgirls","date":"August 14, 2017","excerpt":"<p>An assortment of study designs made it into the Top 10 Most Read articles in July: among the popular studies were a systematic review, a protocol for a prospective observational study, and a qualitative study of service user perceptions. At number one this month is a longitudinal cohort study by Xie et al examining the [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2017/08/14/top-10-most-read-in-july-link-worker-social-prescribing-best-practice-in-sex-and-relationship-education-and-menstrual-hygiene-management-among-bangladeshi-adolescent-schoolgirls/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"6d9e25d8-8c99-505c-af39-bdc0acbdcc83","name":"Highly accessed","slug":"highly-accessed"}]}},{"node":{"id":"f13f94b1-2625-5036-adde-8ef7f98dccb3","title":"Drugs to curb excess stomach acid may be linked to heightened risk of death","slug":"drugs-to-curb-excess-stomach-acid-may-be-linked-to-heightened-risk-of-death","date":"July 03, 2017","excerpt":"<p>May be time to restrict use of widely available proton pump inhibitors, say researchers Proton pump inhibitors (PPIs)—a widely available class of drug designed to curb excess stomach acid production—may be linked to a heightened risk of death, indicates research published in the online journal BMJ Open. Given how widely available these drugs are, and [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2017/07/03/drugs-to-curb-excess-stomach-acid-may-be-linked-to-heightened-risk-of-death/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"d5de7209-af46-5287-b188-3fd371cb1806","title":"Top 10 Most Read in March: Caesarean section and for-profit status of hospitals, mental health links to diet, and weight discrimination","slug":"top-10-most-read-in-march-caesarean-section-and-for-profit-status-of-hospitals-mental-health-links-to-diet-and-weight-discrimination","date":"April 10, 2017","excerpt":"<p>Caesarean sections and for-profit status of hospitals, the link between diet and mental health, and the effect of weight discrimination on physical activity The Top 10 Most Read articles in March showed great variety in topics and study types. In the top spot this month is a systematic review and meta-analysis by Hoxha et al [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2017/04/10/top-10-most-read-in-march-caesarean-section-and-for-profit-status-of-hospitals-mental-health-links-to-diet-and-weight-discrimination/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"6d9e25d8-8c99-505c-af39-bdc0acbdcc83","name":"Highly accessed","slug":"highly-accessed"}]}},{"node":{"id":"cee2c2f7-3fad-592a-a7ce-8eea1572252a","title":"Top 10 Most Read: Negative primary care feedback from minority ethnic patients, higher caesarean sections in for-profit hospitals, adolescents&#8217; sex and drug habits, and biased psychology","slug":"top-10-most-read-negative-primary-care-feedback-from-minority-ethnic-patients-higher-caesarean-sections-in-for-profit-hospitals-adolescents-sex-and-drug-habits-and-biased-psychology","date":"March 13, 2017","excerpt":"<p>&nbsp; &nbsp; February sees five new entries in the top 10 most read articles. At number one this month is an experimental vignette study investigating why minority ethnic groups report poorer primary care experience in patient surveys. Burt and colleagues designed an experiment in the UK to determine whether South Asian people rate simulated GP [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2017/03/13/top-10-most-read-negative-primary-care-feedback-from-minority-ethnic-patients-higher-caesarean-sections-in-for-profit-hospitals-adolescents-sex-and-drug-habits-and-biased-psychology/\">Read More&#8230;</a></p>\n","categories":[{"id":"07acb10c-8a0f-5c7b-8c16-ef2e2c5fd519","name":"About the journal","slug":"about-the-journal"},{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"6d9e25d8-8c99-505c-af39-bdc0acbdcc83","name":"Highly accessed","slug":"highly-accessed"}]}},{"node":{"id":"8f52604f-2f2c-5ef4-8d2e-83f7cdfc8e6d","title":"Identifying individuals at high risk of developing Alzheimer’s disease","slug":"identifying-individuals-at-high-risk-of-developing-alzheimers-disease","date":"March 07, 2017","excerpt":"<p>As we age, we can appreciate how our body changes in our hair, skin, and joints, letting us know that we are getting older. Alongside these obvious changes, our brain starts its own aging process too, although the symptoms might not be as noticeable. This perception mismatch is one of the key challenges in the [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2017/03/07/identifying-individuals-at-high-risk-of-developing-alzheimers-disease/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"}]}},{"node":{"id":"c39c7d74-501c-5349-a3ee-9b17d874c1d4","title":"Paternal involvement in child-rearing, health anxiety, and the global epidemiology of alcohol use: Most read articles in November","slug":"paternal-involvement-in-child-rearing-health-anxiety-and-the-global-epidemiology-of-alcohol-use-most-read-articles-in-november","date":"December 09, 2016","excerpt":"<p>Paternal involvement in early child-rearing, health anxiety and risk of ischaemic heart disease, and the global epidemiology of alcohol use The Top 10 Most Read list for November contains a number of new entries including papers on the reasons for retraction of articles, and the relationship between earlobe creases and coronary artery disease. At number [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2016/12/09/paternal-involvement-in-child-rearing-health-anxiety-and-the-global-epidemiology-of-alcohol-use-most-read-articles-in-november/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"6d9e25d8-8c99-505c-af39-bdc0acbdcc83","name":"Highly accessed","slug":"highly-accessed"}]}},{"node":{"id":"a2730f6d-71f9-59e9-a45f-6e12e8a10fc3","title":"Study reveals lack of supporting evidence for claims about fertility treatments","slug":"study-reveals-lack-of-supporting-evidence-for-claims-about-fertility-treatments","date":"November 28, 2016","excerpt":"<p>Many claims made by UK fertility clinics about the benefits of treatments beyond standard IVF procedures are not backed up by evidence, finds a study published in the online journal BMJ Open. These can range from £50 for a single screening blood test to as much as £8000 for egg freezing packages. The researchers, led [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2016/11/28/study-reveals-lack-of-supporting-evidence-for-claims-about-fertility-treatments/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"ab0c8ced-6415-5cd4-adba-6a52cc3ebba6","title":"Pre-teens whose dads embrace parenthood may be less prone to behavioural issues","slug":"pre-teens-whose-dads-embrace-parenthood-may-be-less-prone-to-behavioural-issues","date":"November 22, 2016","excerpt":"<p>How new fathers see themselves in child’s early years seems to be most influential Kids whose dads adjust well to parenthood and feel confident about their new role may be less likely to have behavioural problems in the run-up to their teens, indicates research published in the online journal BMJ Open. It’s how fathers see [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2016/11/22/pre-teens-whose-dads-embrace-parenthood-may-be-less-prone-to-behavioural-issues/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"5baf189f-5109-5173-a2e3-42c3f4eedba7","title":"London 2012 Olympics inspired many local kids to get more involved in sport","slug":"london-2012-olympics-inspired-many-local-kids-to-get-more-involved-in-sport","date":"November 22, 2016","excerpt":"<p>But levels of physical fitness significantly worsened The London 2012 Olympic Games inspired many local children to get more involved in sport, reveal the results of a before and after study, published in the online journal BMJ Open. But this inspiration did not translate into improved physical fitness, levels of which declined significantly in the [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2016/11/22/london-2012-olympics-inspired-many-local-kids-to-get-more-involved-in-sport/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"bfccac35-59f9-5787-9d55-e588ee7b3b4b","title":"Eyes on Diabetes &#8211; World Diabetes Day 2016","slug":"world-diabetes-day-2016","date":"November 14, 2016","excerpt":"<p>&nbsp; World Diabetes Day (WDD) is celebrated by millions of people around the world on November 14th each year. [1] Led by the IDF, WDD is a global campaign that aims to raise awareness of diabetes and promote efforts to tackle the condition as a global health priority. [2] The International Diabetes Federation (IDF) estimate that 415 [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2016/11/14/world-diabetes-day-2016/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"}]}},{"node":{"id":"27e93124-8a66-5b18-9685-913d01022226","title":"‘Worried well’ may be boosting their risk of heart disease","slug":"worried-well-may-be-boosting-their-risk-of-heart-disease","date":"November 03, 2016","excerpt":"<p>Potential consequences of health anxiety underlines need to treat it, say researchers People who needlessly worry that they have, or will develop, serious illness—popularly referred to as ‘the worried well’—may be boosting their risk of developing heart disease, suggests research published in the online journal BMJ Open. Anxiety is a known risk factor for heart [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2016/11/03/worried-well-may-be-boosting-their-risk-of-heart-disease/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"c9178cc8-8c25-5ab0-b5e9-9d108b548107","title":"Current system unlikely to pick up surgeons with above average patient death rates","slug":"current-system-unlikely-to-pick-up-surgeons-with-above-average-patient-death-rates","date":"October 31, 2016","excerpt":"<p>Performance within ‘expected’ range not good enough to detect outliers, say researchers Publishing the patient death rates of individual surgeons in England is unlikely to pick up those whose mortality rates are above average, because the caseload varies so much, concludes the first analysis of its kind published in the online journal BMJ Open.   Performance [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2016/10/31/current-system-unlikely-to-pick-up-surgeons-with-above-average-patient-death-rates/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"1c43da6c-2bb6-5239-aac8-8fdeef221c2a","title":"Women catching up with men in alcohol consumption and its associated harms","slug":"women-catching-up-with-men-in-alcohol-consumption-and-its-associated-harms","date":"October 24, 2016","excerpt":"<p>Trend most evident among young adults, international analysis shows Women are catching up with men in terms of their alcohol consumption and its impact on their health, finds an analysis of the available international evidence, spanning over a century and published in the online journal BMJ Open. The trend, known as ‘sex convergence’, is most evident [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2016/10/24/women-catching-up-with-men-in-alcohol-consumption-and-its-associated-harms/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"693f4bf2-9e40-5d96-9785-b7a69cc54568","title":"Most read articles in September: young people’s views on school sex education, gender equity in academic medicine and improving patient safety","slug":"most-read-articles-in-september","date":"October 07, 2016","excerpt":"<p>September’s most read list sees several new entries as well as some popular non-movers. This month’s top spot still belongs to Ravnskov et al and their systematic review on the associations between low-density-lipoprotein cholesterol and mortality in the elderly. Pound et al come in at number three with a qualitative synthesis of young people&#8217;s views and [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2016/10/07/most-read-articles-in-september/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"f24b5a9e-ed37-56c1-978a-9c95dbc988a9","name":"Uncategorized","slug":"uncategorized"}]}},{"node":{"id":"acc0dd35-7be3-5a02-bc22-cbc8e0cea007","title":"Guest blog: Improving peer review using peer-reviewed studies #PeerRevWk16","slug":"guest-blog-improving-peer-review-using-peer-reviewed-studies","date":"September 19, 2016","excerpt":"<p>This week is the second ever “peer review week”. The theme for this year is &#8220;Recognition for Review&#8221;. Peer review week aims to highlight the importance of peer review, which is a crucial part of the research process. We asked Dr Adrian Barnett, from the Queensland University of Technology, and a member of our editorial board, to survey [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2016/09/19/guest-blog-improving-peer-review-using-peer-reviewed-studies/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"63c422c3-558c-5df7-8204-be0e85131f8c","name":"Journalology","slug":"journalology"},{"id":"73eb17de-115a-54c2-9859-6782b36cb1d0","name":"Open peer review","slug":"open-peer-review"}]}},{"node":{"id":"d7c46d72-c02d-5d0e-b2c4-525799ef7292","title":"School sex education often negative, heterosexist, and out of touch","slug":"school-sex-education-often-negative-heterosexist-and-out-of-touch","date":"September 12, 2016","excerpt":"<p>And taught by poorly trained, embarrassed teachers, say young people School sex education is often negative, heterosexist, and out of touch, and taught by poorly trained, embarrassed teachers, finds a synthesis of the views and experiences of young people in different countries, published in the online journal BMJ Open. Schools’ failure to acknowledge that sex education [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2016/09/12/school-sex-education-often-negative-heterosexist-and-out-of-touch/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"be63a9b4-1afd-514b-92dd-7ec7d31aaffa","title":"Volunteering and mental health, breast feeding outcomes and condom use intentions: Most read articles in August","slug":"volunteering-and-mental-health-breast-feeding-outcomes-and-condom-use-intentions-most-read-articles-in-august","date":"September 07, 2016","excerpt":"<p>Association of volunteering and mental well-being, breast feeding outcomes and place of birth, and condom use intentions of heterosexual men The August most read list contains papers with a number of different study designs, on a number of different topics. Returning to the top spot this month is a systematic review on the lack of [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2016/09/07/volunteering-and-mental-health-breast-feeding-outcomes-and-condom-use-intentions-most-read-articles-in-august/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"6d9e25d8-8c99-505c-af39-bdc0acbdcc83","name":"Highly accessed","slug":"highly-accessed"}]}},{"node":{"id":"508a74f0-0f39-5d45-867a-58bb2190aa5b","title":"Volunteering in middle age and senior years linked to enhanced mental health","slug":"volunteering-in-middle-age-and-senior-years-linked-to-enhanced-mental-health","date":"August 08, 2016","excerpt":"<p>No positive association seen before age of 40, suggesting link varies across life course Volunteering in middle and older age is linked to good mental health/emotional wellbeing, finds a large study of British adults, published in the online journal BMJ Open. But no such association was seen before the age of 40, suggesting that the [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2016/08/08/volunteering-in-middle-age-and-senior-years-linked-to-enhanced-mental-health/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"3a5dc571-92ef-5aef-a94b-34e86f0df236","title":"Third to half of UK population lives with chronic pain","slug":"third-to-half-of-uk-population-lives-with-chronic-pain","date":"June 20, 2016","excerpt":"<p>Proportion likely to rise as population ages; major cause of disability and distress Between a third and half (43%) of the UK population—roughly 28 million adults—lives with chronic pain, finds an analysis of the available evidence, published in the online journal BMJ Open. This proportion is likely to rise as the population ages, warn the [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2016/06/20/third-to-half-of-uk-population-lives-with-chronic-pain/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"ea054f60-a2c3-5356-ab75-ac9ca3e71881","title":"Drug treatment of hyperactivity in kids may have levelled off in UK","slug":"drug-treatment-of-hyperactivity-in-kids-may-have-levelled-off-in-uk","date":"June 20, 2016","excerpt":"<p>But it lasts much longer than it does in rest of Europe or US The tendency to treat childhood hyperactivity (ADHD) with drugs may have reached a plateau in the UK, following a steep rise in the number of prescriptions for these medicines over the past 20 years, reveals research published in the online journal BMJ [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2016/06/20/drug-treatment-of-hyperactivity-in-kids-may-have-levelled-off-in-uk/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"f8c8520e-4b0a-58fc-9a1e-094dc4b4adc5","title":"Prolonged sitting, non-fatal self-harm, and institutional mental healthcare: Most read articles in May","slug":"prolonged-sitting-non-fatal-self-harm-and-institutional-mental-healthcare-most-read-articles-in-may","date":"June 06, 2016","excerpt":"<p>Prolonged sitting and fatigue, non-fatal self-harm, and trends in institutional mental healthcare in Western Europe This month sees a mixture of some old favourites and some new entries in the Top 10 Most Read list. First up is a pilot study by Wennberg et al which uses a randomised two-condition crossover trial to compare the acute effects of uninterrupted sitting [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2016/06/06/prolonged-sitting-non-fatal-self-harm-and-institutional-mental-healthcare-most-read-articles-in-may/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"6d9e25d8-8c99-505c-af39-bdc0acbdcc83","name":"Highly accessed","slug":"highly-accessed"}]}},{"node":{"id":"23b1e74b-038c-581a-a505-b059fd9d73c2","title":"Celebrating International Nurses Day #IND2016","slug":"celebrating-international-nurses-day-ind2016","date":"May 12, 2016","excerpt":"<p>&nbsp; International Nurses Day, celebrated on 12th May each year and coinciding with Florence Nightingale&#8217;s birthday, marks the important contributions nurses make to society. As the single largest group of health professionals, nurses care from the first moments of life right through to the final days. To show our appreciation of the work that nurses [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2016/05/12/celebrating-international-nurses-day-ind2016/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"93d70e43-a898-5f17-98af-3cb280997e3c","name":"Open access","slug":"open-access"}]}},{"node":{"id":"52d76cdc-c66f-5b02-b7fa-1d61ca469fc9","title":"Radiotherapy during surgery could save millions of travel miles and tonnes of CO2","slug":"radiotherapy-during-surgery-could-save-millions-of-travel-miles-and-tonnes-of-co2","date":"May 09, 2016","excerpt":"<p>…plus free up thousands of hours for women with early stage breast cancer, every year One targeted dose of radiotherapy given during surgery to remove early stage breast cancer could save millions of travel miles, enough CO2 emissions for a 100 hectare forest, and free up thousands of hours of women’s time, concludes research published [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2016/05/09/radiotherapy-during-surgery-could-save-millions-of-travel-miles-and-tonnes-of-co2/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"a90ad787-e8e0-5142-b92a-5c9f5c9490a7","title":"Prenatal exposure to cannabis, overdiagnosis, and counterfeit medicines: Most read articles in April","slug":"prenatal-exposure-to-cannabis-overdiagnosis-and-counterfeit-medicines-most-read-articles-in-april","date":"May 09, 2016","excerpt":"<p>Prenatal exposure to cannabis, public definitions of overdiagnosis, and counterfeit medicines in Peru The Top 10 Most Read papers list this month contains a number of new entries, many of which have been published within the current issue. They come from a wide range of of areas and some have attracted attention via press release, blogs and discussion [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2016/05/09/prenatal-exposure-to-cannabis-overdiagnosis-and-counterfeit-medicines-most-read-articles-in-april/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"6d9e25d8-8c99-505c-af39-bdc0acbdcc83","name":"Highly accessed","slug":"highly-accessed"}]}},{"node":{"id":"0fed5230-00a7-5d86-be90-9719bd509a37","title":"Hospital self-harm cases have steadily risen among men in England since 2008","slug":"hospital-self-harm-cases-have-steadily-risen-among-men-in-england-since-2008","date":"April 28, 2016","excerpt":"<p>But only half of those going to hospital afterwards get mental health assessment The number of hospital cases of self-inflicted harm, such as cutting and overdosing on prescription meds, has risen steadily since 2008 in England among men, reveals research published in the online journal BMJ Open. But only around half of those who go [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2016/04/28/hospital-self-harm-cases-have-steadily-risen-among-men-in-england-since-2008/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"1249c8cf-0cf8-5e50-8ff6-d3ac45d4869a","title":"Metal hip replacements implanted since 2006 more prone to failure","slug":"metal-hip-replacements-implanted-since-2006-more-prone-to-failure","date":"April 28, 2016","excerpt":"<p>Higher rate of issues in manufacturing process since this date may be to blame, say researchers Metal on metal hip replacements implanted since 2006 are more prone to failure and the need for further surgery, finds research looking at revision rates at one hospital trust for the DePuy Pinnacle device, and published in the online [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2016/04/28/metal-hip-replacements-implanted-since-2006-more-prone-to-failure/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"3fcc6966-714b-515b-88fc-118e1d84cfd8","title":"Ultra-processed foods, drinks marketed to children, and cannabis use in psychosis: Most read articles in March","slug":"ultra-processed-foods-drinks-marketed-to-children-and-cannabis-use-in-psychosis-most-read-articles-in-march","date":"April 08, 2016","excerpt":"<p>Ultra-processed foods and added sugars, the content of drinks marketed to children, and cannabis use in first episode psychosis March was a busy month here at BMJ Open, with many new papers entering the Top 10 Most Read. In at number one this month is a popular paper which was press released and saw a lot [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2016/04/08/ultra-processed-foods-drinks-marketed-to-children-and-cannabis-use-in-psychosis-most-read-articles-in-march/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"6d9e25d8-8c99-505c-af39-bdc0acbdcc83","name":"Highly accessed","slug":"highly-accessed"}]}},{"node":{"id":"5f2e2ebd-3a28-51ce-a261-f97c8e6fe125","title":"Cannabis use in pregnancy linked to low birthweight and intensive care","slug":"cannabis-use-in-pregnancy-linked-to-low-birthweight-and-intensive-care","date":"April 05, 2016","excerpt":"<p>As drug gains social acceptance, doctors and women need to be better informed, say researchers Use of cannabis during pregnancy is linked to low birthweight and the need for intensive care, reveals an analysis of the available evidence, published in the online journal BMJ Open. As cannabis becomes more socially acceptable, it’s important that prospective mums-to-be and [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2016/04/05/cannabis-use-in-pregnancy-linked-to-low-birthweight-and-intensive-care/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"40b189b7-fb19-5aae-af76-09687fdfa05a","title":"Sugar content in fruit drinks marketed to kids “unacceptably high”","slug":"sugar-content-in-fruit-drinks-marketed-to-kids-unacceptably-high","date":"March 23, 2016","excerpt":"<p>Almost half of products assessed contain entire daily recommended maximum intake The sugar content of fruit drinks—including natural fruit juices and smoothies—is “unacceptably high,” with smoothies among the worst offenders, finds research published in the online journal BMJ Open. Almost half the products assessed contained at least a child’s entire daily recommended maximum sugar intake [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2016/03/23/sugar-content-in-fruit-drinks-marketed-to-kids-unacceptably-high/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"5bc4345a-699d-551c-8986-b1ccfd5b1299","title":"Medical writing support, postnuclear disaster evacuation, and publication culture: Most read articles in February","slug":"medical-writing-support-postnuclear-disaster-evacuation-and-publication-culture-most-read-articles-in-february","date":"March 10, 2016","excerpt":"<p>Medical writing support and the quality of reporting, postnuclear disaster evacuation, and the current publication culture Once again, this month sees a selection of new papers entering the Top 10 Most Read list, alongside some old favourites. Returning to the top spot this month is Teschke et al‘s paper on bicycling injury hospitalisation rates, which continues to attract attention and [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2016/03/10/medical-writing-support-postnuclear-disaster-evacuation-and-publication-culture-most-read-articles-in-february/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"6d9e25d8-8c99-505c-af39-bdc0acbdcc83","name":"Highly accessed","slug":"highly-accessed"}]}},{"node":{"id":"b992f30e-1b53-5be4-afc7-afc64602c622","title":"‘Ultra-processed’ foods make up more than half of all calories in US diet","slug":"ultra-processed-foods-make-up-more-than-half-of-all-calories-in-us-diet","date":"March 09, 2016","excerpt":"<p>&#8230;And contribute 90 per cent of all dietary added sugar intake ‘Ultra-processed’ foods make up more than half of all calories consumed in the US diet, and contribute nearly 90% of all added sugar intake, finds research published in the online journal BMJ Open. Ultra-processed foods are formulations of several ingredients. Besides salt, sugar, oils and [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2016/03/09/ultra-processed-foods-make-up-more-than-half-of-all-calories-in-us-diet/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"edb21e8c-4bb5-5790-a64c-091e9c914f1a","title":"Higher nurse to patient ratio linked to reduced risk of inpatient death","slug":"higher-nurse-to-patient-ratio-linked-to-reduced-risk-of-inpatient-death","date":"February 09, 2016","excerpt":"<p>Death rates 20 per cent lower in hospitals with 6 or fewer patients per registered nurse A higher nurse to patient ratio is linked to a reduced risk of inpatient death, finds a study of staffing levels in NHS hospitals, published in the online journal BMJ Open. In trusts where registered (professionally trained) nurses had [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2016/02/09/higher-nurse-to-patient-ratio-linked-to-reduced-risk-of-inpatient-death/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"d0fe4cd4-4abc-521d-b3c4-618f5a834127","title":"The NHS Health Check in England, bereavement by suicide, and public involvement in medical research: Most read articles in January","slug":"the-nhs-health-check-in-england-bereavement-by-suicide-and-public-involvement-in-medical-research-most-read-articles-in-january","date":"February 05, 2016","excerpt":"<p>NHS Health Checks, the risks of bereavement by suicide, and public involvement in medical research The Top 10 Most Read list for January is almost completely comprised of new entries, with the exception of the ever popular review by Kristensen et al on the effects of statins. In the top spot this month is an evaluation [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2016/02/05/the-nhs-health-check-in-england-bereavement-by-suicide-and-public-involvement-in-medical-research-most-read-articles-in-january/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"6d9e25d8-8c99-505c-af39-bdc0acbdcc83","name":"Highly accessed","slug":"highly-accessed"}]}},{"node":{"id":"880ec165-d53c-542b-965f-62d7fbdb1aba","title":"Antidepressants and risk of mania, medically unexplained symptoms and the association between alcohol and mortality in Alzheimer&#8217;s: Most read articles in December","slug":"antidepressants-and-risk-of-mania-medically-unexplained-symptoms-and-the-association-between-alcohol-and-mortality-in-alzheimers-most-read-articles-in-december","date":"January 06, 2016","excerpt":"<p>Antidepressants, medically unexplained symptoms, and alcohol consumption in Alzheimer&#8217;s patients December saw a lot of new papers entering the Top 10 Most Read, with half of them being from the current issue. Patel et al&#8216;s research paper, complete with video abstract and press release, questions whether antidepressants increase the risk of mania and bipolar disorder [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2016/01/06/antidepressants-and-risk-of-mania-medically-unexplained-symptoms-and-the-association-between-alcohol-and-mortality-in-alzheimers-most-read-articles-in-december/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"6d9e25d8-8c99-505c-af39-bdc0acbdcc83","name":"Highly accessed","slug":"highly-accessed"}]}},{"node":{"id":"6ed409ca-55cf-57dd-8631-520c43fe2eca","title":"Certain antidepressants linked to heightened risk of mania and bipolar disorder","slug":"certain-antidepressants-linked-to-heightened-risk-of-mania-and-bipolar-disorder","date":"December 16, 2015","excerpt":"<p>Strongest association found for SSRIs and venlafaxine Taking certain antidepressants for depression is linked to a heightened risk of subsequent mania and bipolar disorder, reveals research published in the online journal BMJ Open. The strongest association seemed to be for serotonin reuptake inhibitors, or SSRIs for short, and the dual action antidepressant venlafaxine, the analysis [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2015/12/16/certain-antidepressants-linked-to-heightened-risk-of-mania-and-bipolar-disorder/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"8cfa3f26-b5f5-5159-b750-cc0eab29c015","title":"Bicycling injury hospitalisation rates, clinical trial registration and women in leadership roles: Most read articles in November","slug":"bicycling-injury-hospitalisation-rates-clinical-trial-registration-and-women-in-leadership-roles-most-read-articles-in-november","date":"December 09, 2015","excerpt":"<p>Bicycling injuries, clinical trial registration, and the under-representation of women in leadership roles This month sees a lot of change in the Top 10 Most Read, with many new papers entering the list. At number one this month is Teschke et al&#8216;s assessment of bicycling injury hospitalisation rates with relation to helmet legislation and mode shares, [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2015/12/09/bicycling-injury-hospitalisation-rates-clinical-trial-registration-and-women-in-leadership-roles-most-read-articles-in-november/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"6d9e25d8-8c99-505c-af39-bdc0acbdcc83","name":"Highly accessed","slug":"highly-accessed"}]}},{"node":{"id":"7cd418a4-b2d9-5cb6-b641-30c82bcd0bfc","title":"US pedestrian wheelchair users third more likely to die in car crashes than general public","slug":"us-pedestrian-wheelchair-users-third-more-likely-to-die-in-car-crashes-than-general-public","date":"November 19, 2015","excerpt":"<p>And men’s risk is five times higher than women’s, particularly among 50 to 64 year olds Pedestrian wheelchair users in the US are a third more likely to be killed in road traffic collisions than the general public, finds research published in the online journal BMJ Open. And men’s risk is five times higher than women’s, [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2015/11/19/us-pedestrian-wheelchair-users-third-more-likely-to-die-in-car-crashes-than-general-public/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"2989b376-f870-5964-a103-8737524946c2","title":"The effect of statins, obesity-related behaviours in Europe and the accuracy of Wikipedia content: Most read articles in October","slug":"the-effect-of-statins-obesity-related-behaviours-in-europe-and-the-accuracy-of-wikipedia-content-most-read-articles-in-october","date":"November 09, 2015","excerpt":"<p>Statins, obesity-related behaviours, and the accuracy of Wikipedia &nbsp; Several papers have entered the Top 10 Most Read this month, including Kristensen et al&#8216;s systematic review on statins and survival in randomised trials, which tops the list. In our tenth most read article, Azer et al assess the accuracy of content of Wikipedia articles on cardiovascular diseases, and at number [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2015/11/09/the-effect-of-statins-obesity-related-behaviours-in-europe-and-the-accuracy-of-wikipedia-content-most-read-articles-in-october/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"6d9e25d8-8c99-505c-af39-bdc0acbdcc83","name":"Highly accessed","slug":"highly-accessed"}]}},{"node":{"id":"e98cb3be-f8c6-56c4-acb3-1391b6f19677","title":"Forum 2015: Global Forum on Research and Innovation for Health","slug":"forum-2015-global-forum-on-research-and-innovation-for-health","date":"September 08, 2015","excerpt":"<p>With the aim of identifying solutions to the world’s unmet health needs through research and innovation, Forum 2015 provided delegates with a unique global platform on which to present ideas for innovation in health and create partnerships for action. The Council on Health Research for Development (COHRED) partnered with the Philippines for this event, which [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2015/09/08/forum-2015-global-forum-on-research-and-innovation-for-health/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"5e974932-e700-54c2-a58f-b9402ea0f9f4","name":"Events","slug":"events"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"12be2875-cc67-5bd6-8ee6-16e51b0f2082","title":"Thank you to our reviewers &#8211; 2014","slug":"thank-you-to-our-reviewers-2014","date":"February 18, 2015","excerpt":"<p>After a very busy year at BMJ Open, in which over 1100 papers were published, we would like to say a big thank you to all of our reviewers who contributed in 2014. All that we achieved last year would not have been possible without the help of the many referees who gave thorough and [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2015/02/18/thank-you-to-our-reviewers-2014/\">Read More&#8230;</a></p>\n","categories":[{"id":"07acb10c-8a0f-5c7b-8c16-ef2e2c5fd519","name":"About the journal","slug":"about-the-journal"},{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"b9d4de2a-7eda-5a44-9d0e-5348fe6892f3","name":"Discounts","slug":"discounts"},{"id":"93d70e43-a898-5f17-98af-3cb280997e3c","name":"Open access","slug":"open-access"},{"id":"73eb17de-115a-54c2-9859-6782b36cb1d0","name":"Open peer review","slug":"open-peer-review"}]}},{"node":{"id":"22be118c-5e83-5cf3-b3ae-b9973419d883","title":"Erik Martin: Author Profile","slug":"erik-martin-author-profile","date":"November 21, 2014","excerpt":"<p>We are happy to introduce a new feature called Author Profiles where we interview BMJ Open authors to find out more about them and their work. For our first author profile, we spoke to Erik Martin from Deakin University, Australia, to find out more about his research and his thoughts on Open Access. Erik is [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2014/11/21/erik-martin-author-profile/\">Read More&#8230;</a></p>\n","categories":[{"id":"dc49b913-f070-5a12-99c2-65b2799a519f","name":"Author Profile","slug":"author-profile"},{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"93d70e43-a898-5f17-98af-3cb280997e3c","name":"Open access","slug":"open-access"}]}},{"node":{"id":"1d2e4b93-1a04-5d71-bc16-f6d1656fa136","title":"BMJ Open now publishes cohort profiles","slug":"bmj-open-now-publishes-cohort-profiles","date":"August 22, 2014","excerpt":"<p>&nbsp; BMJ Open currently publishes articles reporting research results or study protocols. We have now expanded our scope to include cohort profiles, articles that describe major, ongoing research cohorts. What’s the difference between a protocol, a cohort profile and a research paper? Detailed information about cohort profiles is in our instructions for authors. In brief, cohort [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2014/08/22/bmj-open-now-publishes-cohort-profiles/\">Read More&#8230;</a></p>\n","categories":[{"id":"07acb10c-8a0f-5c7b-8c16-ef2e2c5fd519","name":"About the journal","slug":"about-the-journal"},{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"63c422c3-558c-5df7-8204-be0e85131f8c","name":"Journalology","slug":"journalology"}]}},{"node":{"id":"dd62b4a8-1d47-5fb0-9b28-59415f5ab535","title":"Bringing old trials to light in BMJ Open","slug":"bringing-old-trials-to-light-in-bmj-open","date":"May 14, 2014","excerpt":"<p>&nbsp; Today we have published the first trial prompted by the Restoring Invisible and Abandoned Trials (RIAT) initiative. Dr Tom Treasure from UCL, with colleagues from University of Sussex and Imperial College, have brought back from obscurity the results of the ‘CEA Second-Look’ trial. The study asked the question: in patients who have undergone a [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2014/05/14/bringing-old-trials-to-light-in-bmj-open/\">Read More&#8230;</a></p>\n","categories":[{"id":"07acb10c-8a0f-5c7b-8c16-ef2e2c5fd519","name":"About the journal","slug":"about-the-journal"},{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"},{"id":"63c422c3-558c-5df7-8204-be0e85131f8c","name":"Journalology","slug":"journalology"}]}},{"node":{"id":"762ac038-558d-5978-9691-993caf5b463c","title":"Thank you to our reviewers &#8211; 2013","slug":"thank-you-to-our-reviewers-2013","date":"January 16, 2014","excerpt":"<p>Peer review is a fundamental part of publishing. Perhaps nowhere more so than in the open access field, which is often more scrutinised than other traditional publishing routes. Recognising this, the BMJ Open team would like to thank all 2725 peer reviewers who refereed for the journal in 2013 &#8211; your advice and considered remarks [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2014/01/16/thank-you-to-our-reviewers-2013/\">Read More&#8230;</a></p>\n","categories":[{"id":"07acb10c-8a0f-5c7b-8c16-ef2e2c5fd519","name":"About the journal","slug":"about-the-journal"},{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"b9d4de2a-7eda-5a44-9d0e-5348fe6892f3","name":"Discounts","slug":"discounts"},{"id":"93d70e43-a898-5f17-98af-3cb280997e3c","name":"Open access","slug":"open-access"},{"id":"73eb17de-115a-54c2-9859-6782b36cb1d0","name":"Open peer review","slug":"open-peer-review"}]}},{"node":{"id":"8043666f-0fcf-54b3-9e78-8eb8fe041d9e","title":"Using the SPIRIT statement to improve trial protocols","slug":"using-the-spirit-statement-to-improve-trial-protocols","date":"January 18, 2013","excerpt":"<p>We have updated our instructions for authors to show that we now encourage the use of the SPIRIT statement. SPIRIT (Standard Protocol Items: Recommendations for Interventional Trials) is ‘an international initiative that aims to improve the quality of clinical trial protocols by defining an evidence-based set of items to address in a protocol’. Its creation [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2013/01/18/using-the-spirit-statement-to-improve-trial-protocols/\">Read More&#8230;</a></p>\n","categories":[{"id":"07acb10c-8a0f-5c7b-8c16-ef2e2c5fd519","name":"About the journal","slug":"about-the-journal"},{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"63c422c3-558c-5df7-8204-be0e85131f8c","name":"Journalology","slug":"journalology"}]}},{"node":{"id":"3b5394aa-df5e-57e9-accf-bfcbe1e34de3","title":"2012 year in review","slug":"2012-year-in-review","date":"January 11, 2013","excerpt":"<p>&nbsp; 2012 was a hugely successful year for BMJ Open. We published four times as many papers as in 2011. Credit for this must go, first and foremost, to the hundreds of reviewers who have given their time to assess manuscripts. We are also grateful that so many authors have chosen to publish with us. [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2013/01/11/2012-year-in-review/\">Read More&#8230;</a></p>\n","categories":[{"id":"07acb10c-8a0f-5c7b-8c16-ef2e2c5fd519","name":"About the journal","slug":"about-the-journal"},{"id":"69390d22-95b7-5e16-9983-1300b1b77c1c","name":"BMJ Open Editions","slug":"bmj-open-editions"},{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"b9d4de2a-7eda-5a44-9d0e-5348fe6892f3","name":"Discounts","slug":"discounts"},{"id":"6d9e25d8-8c99-505c-af39-bdc0acbdcc83","name":"Highly accessed","slug":"highly-accessed"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"a7cfc460-ac2d-53d8-9b48-90f7893b19fa","title":"Most popular articles in August","slug":"most-popular-articles-in-august","date":"September 06, 2011","excerpt":"<p>  The list of the top 10 most-read articles in BMJ Open in August is topped by a protocol that sets out an implementation research study to assess the surgical scale-up process in the rural district of Achham, Nepal. You can see it here. There are three protocols in the top 10.   Authors Title 1 D [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2011/09/06/most-popular-articles-in-august/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"6d9e25d8-8c99-505c-af39-bdc0acbdcc83","name":"Highly accessed","slug":"highly-accessed"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}},{"node":{"id":"c2b41132-12e8-5bd7-a854-fce0efb225fd","title":"Most popular articles in July","slug":"most-popular-articles-in-july","date":"August 10, 2011","excerpt":"<p>  The list of the top 10 most-read articles in BMJ Open in July includes some recent content and some older papers. It&#8217;s good to see a range of topics covered, and two study protocols included also. Engkilde et al.&#8217;s recent paper on the association between cancer and contact allergy received considerable coverage in the press [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"https://blogs.bmj.com/bmjopen/2011/08/10/most-popular-articles-in-july/\">Read More&#8230;</a></p>\n","categories":[{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"},{"id":"6d9e25d8-8c99-505c-af39-bdc0acbdcc83","name":"Highly accessed","slug":"highly-accessed"},{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}]}}]}},"pathContext":{"slug":"content","name":"Content"}}

